.container{
    /* border: 2px solid #000000; */
    /* height: 49rem; */
    height: 49.3rem;
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    margin-left: 0;
    margin-right: 0;
    max-width: none;
    padding: 0;
    background-color: #f7f7f7;
}

.initial {
    border: none;
    background-color: #54c3ff;
}
.wronglyOrdered {
    border: none;
    background-color: #f75959;
}
.inComparison {
    border: none;
    background-color: #fff563;
}

.sorted {
    border: none;
    background-color: #d7fc03;
}
